import React, { Component } from 'react'
import Chat from './Chat'
import ChannelBar from './ChannelBar'
import io from 'socket.io-client'
import './App.css'
const socket = io('35.165.137.171')

class App extends Component {
  state = {
    user: '',
    loggedIn: false,
    socket: {},
    messages: [
      { author: 'Admin', message: 'Welcome to BrainStation chat! Please be civil.' }
    ],
    channels: []
  }

  componentDidMount() {
    socket.connect()
    socket.on('connect', ()=>{
      this.setState({
        socket: socket
      })
		  socket.emit('adduser', prompt("What's your name?"))
	  })

    socket.on('client:joinchat', (username, data)=>{
      this.setState({
        user: username,
        messages: [{ author: 'Admin', message: `Welcome to ${data}! Please be civil.` }]
      })
    })

    socket.on('updatechat', (username, data)=>{
      this.setState({
        messages: this.state.messages.concat({author: username, message:data})
      })
    })

    socket.on('client:channelerror', (user,data)=>{
      this.setState({
        messages: this.state.messages.concat({author:user, message:data})
      })
    })

    socket.on('updaterooms', (rooms, current_room)=>{
      this.setState(prevState=>prevState.channels = rooms)
    })
  }

  submitChat = e =>{
    e.preventDefault()
    socket.emit('sendchat', this.textToSend.value)
    this.textToSend.value = ''
  }

  changeRoom = val =>{
    socket.emit('switchRoom', val)
  }

  addRoom = e =>{
    e.preventDefault()
    console.log('Trying to add room', this.room.value)
    socket.emit('addroom', {roomname: this.room.value})
    this.room.value = ''
  }

  render() {
    return (
      <div className="App">
        <h1> Welcome to BrainStation Chat! </h1>
        <ChannelBar changeChannel={this.changeRoom} channels={this.state.channels}/>
        <Chat messages={this.state.messages} />
        <form className="chatbox" onSubmit={this.submitChat}>
          <input ref={ref => this.textToSend = ref} type="text" placeholder="Talk to the class!" />
        </form>
        <form className='addRoom' onSubmit={this.addRoom}>
          <input ref={ref => this.room = ref} type='text' placeholder='Add a new room to the list!'/>
        </form>
      </div>
    );
  }
}

export default App



